/* Highlighting styles */
.highlight-path {
  background-color: #FFFDE7;
}

.highlight-key {
  background-color: #FFEB3B;
  padding: 0 4px;
  border-radius: 2px;
}

.highlight-value {
  background-color: #FFEB3B;
  padding: 0 4px;
  border-radius: 2px;
}

/* Toggle button for JSON view */
.toggle-json {
  font-size: 0.8rem;
  background-color: #eaeaea;
  color: #555;
  padding: 2px 8px;
  border-radius: 4px;
  margin-left: 10px;
  cursor: pointer;
  border: none;
}

.toggle-json:hover {
  background-color: #d5d5d5;
}

.header-buttons {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-top: 10px;
}

.add-section-button {
  background-color: #27ae60;
  color: white;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  border: none;
  transition: background-color 0.3s;
}

.add-section-button:hover {
  background-color: #219653;
}

/* Path details section */
.path-details {
  margin-top: 15px;
  padding: 15px;
  background-color: #f0f7ff;
  border-radius: 4px;
  border-left: 4px solid #3498db;
}

.path-details h4 {
  margin-top: 0;
  margin-bottom: 10px;
  color: #2c3e50;
  font-size: 1.1rem;
}

.path-details p {
  margin-bottom: 8px;
}

.properties-list {
  margin-top: 5px;
  padding-left: 20px;
  list-style-type: none;
  max-height: 200px;
  overflow-y: auto;
}

.properties-list li {
  padding: 3px 0;
  display: flex;
}

.property-key {
  font-weight: 500;
  margin-right: 10px;
  color: #0067b8;
}

.property-type {
  color: #717171;
  font-style: italic;
}

/* Section description */
.section-description {
  margin: 5px 0 15px 0;
  padding: 8px 12px;
  background-color: #f5f9ff;
  border-left: 3px solid #3498db;
  color: #555;
  font-size: 0.9rem;
  font-style: italic;
  line-height: 1.4;
}

.section-path {
  margin-bottom: 5px;
  padding-bottom: 5px;
  border-bottom: 1px dotted #ccc;
  font-style: normal;
  color: #0067b8;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.6;
  color: #333;
  background-color: #f5f5f5;
}

.app-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.header {
  text-align: center;
  margin-bottom: 20px;
}

.header h1 {
  margin-bottom: 15px;
  color: #2c3e50;
}

input[type="file"] {
  display: none;
}

.upload-button {
  display: inline-block;
  background-color: #3498db;
  color: white;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.upload-button:hover {
  background-color: #2980b9;
}

.main-content {
  display: flex;
  gap: 20px;
  min-height: calc(100vh - 150px);
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.sidebar {
  width: 250px;
  background-color: #2c3e50;
  color: white;
  padding: 15px;
  border-radius: 5px 0 0 5px;
}

.sidebar h2 {
  font-size: 1.2rem;
  margin-bottom: 15px;
  padding-bottom: 10px;
  border-bottom: 1px solid #3a506b;
}

.sidebar-item {
  padding: 10px 15px;
  border-radius: 4px;
  margin-bottom: 5px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.sidebar-item:hover {
  background-color: #34495e;
}

.sidebar-item.active {
  background-color: #3498db;
}

.json-viewer {
  flex: 1;
  background-color: white;
  padding: 20px;
  overflow-y: scroll; /* Changed from auto to scroll to ensure scrollability */
  border-radius: 0 5px 5px 0;
  position: relative;
  height: calc(100vh - 150px); /* Ensure it has a defined height */
  max-height: calc(100vh - 150px);
}

.json-viewer h2 {
  color: #2c3e50;
  margin-bottom: 15px;
}

.section-content {
  margin-bottom: 30px;
}

.section-content h3 {
  color: #3498db;
  margin-bottom: 10px;
}

.json-section {
  background-color: #f8f9fa;
  padding: 15px;
  border-radius: 4px;
  overflow-x: auto;
  margin-bottom: 20px;
}

.full-json {
  border-top: 1px solid #eee;
  padding-top: 20px;
}

.full-json h3 {
  color: #2c3e50;
  margin-bottom: 10px;
}

pre {
  background-color: #f8f9fa;
  padding: 15px;
  border-radius: 4px;
  overflow-x: auto;
  font-family: 'Courier New', Courier, monospace;
  font-size: 14px;
}

.no-data {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
  background-color: #f8f9fa;
  border-radius: 4px;
  color: #7f8c8d;
}

.not-found {
  color: #e74c3c;
  font-style: italic;
}

/* Scroll buttons */
.scroll-buttons {
  position: fixed;
  right: 30px;
  bottom: 30px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  opacity: 1; /* Always visible for testing */
  transition: opacity 0.3s ease;
  pointer-events: all; /* Enable interactions for testing */
  z-index: 1000;
}

.scroll-buttons.visible {
  opacity: 1;
  pointer-events: all;
}

.scroll-button {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #e74c3c; /* More visible red color */
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: 28px;
  border: 2px solid white;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.3);
  transition: background-color 0.2s, transform 0.2s;
}

.scroll-button:hover {
  background-color: #c0392b;
  transform: scale(1.1);
}

.scroll-button:active {
  transform: scale(0.95);
}